'use client';

import React from "react";
import { useTheme } from './context/ThemeContext';
import { createStyles } from './utils/styles';
import { GlassCard } from './shared/SharedComponents';

// ============================================================================
// HEADER COMPONENT
// ============================================================================
const Header = () => {
  const { isDark, toggleTheme } = useTheme();
  const styles = createStyles(isDark);

  return (
    <header className="sticky top-0 z-[100] p-6 backdrop-blur-xl">
      <GlassCard style={{ padding: '2rem' }}>
        <div className="flex justify-between items-center gap-8">
          <div className="flex items-center gap-6">
            <div 
              className="text-6xl rounded-3xl p-2 flex items-center justify-center shadow-2xl"
              style={{
                background: isDark 
                  ? 'linear-gradient(135deg, #60a5fa, #a78bfa, #f472b6)'
                  : 'linear-gradient(135deg, #3b82f6, #8b5cf6, #ec4899)',
                boxShadow: isDark 
                  ? '0 8px 32px rgba(139, 92, 246, 0.3)'
                  : '0 8px 32px rgba(59, 130, 246, 0.2)'
              }}
            >
              üìä
            </div>
            <div>
              <h1 
                className="m-0 text-5xl font-black leading-tight tracking-tighter"
                style={{
                  fontFamily: "'Inter', -apple-system, sans-serif",
                  ...styles.gradientText,
                  textShadow: isDark ? '0 2px 4px rgba(0, 0, 0, 0.3)' : 'none'
                }}
              >
                Survey Explorer
              </h1>
              <p 
                className="mt-2 mb-0 text-lg font-semibold tracking-wide"
                style={{
                  color: isDark ? '#94a3b8' : '#64748b',
                  fontFamily: "'Inter', -apple-system, sans-serif"
                }}
              >
                Professional Data Analytics Platform
              </p>
            </div>
          </div>
          
          <button 
            onClick={toggleTheme}
            className="w-[70px] h-[70px] rounded-3xl flex items-center justify-center text-3xl cursor-pointer transition-all duration-500 ease-out border-2 border-white/20"
            style={{
              background: isDark 
                ? 'linear-gradient(135deg, #6366f1, #8b5cf6)'
                : 'linear-gradient(135deg, #3b82f6, #6366f1)',
              boxShadow: isDark 
                ? '0 8px 32px rgba(139, 92, 246, 0.4)'
                : '0 8px 32px rgba(59, 130, 246, 0.3)'
            }}
            onMouseEnter={(e) => {
              e.currentTarget.style.transform = 'scale(1.1) rotate(180deg)';
            }}
            onMouseLeave={(e) => {
              e.currentTarget.style.transform = 'scale(1) rotate(0deg)';
            }}
          >
            {isDark ? '‚òÄÔ∏è' : 'üåô'}
          </button>
        </div>
      </GlassCard>
    </header>
  );
};

export default Header;
