'use client';

import React, { useState } from "react";
import { useTheme } from './context/ThemeContext';
import { createStyles } from './utils/styles';
import { GlassCard, FormField, StatusBadge, LoadingSpinner } from './shared/SharedComponents';

interface FormData {
  survey: string;
  year: string;
  gender: string;
  ageMin: string;
  ageMax: string;
  resultType: string;
  keyword: string;
}

interface AnalysisParametersFormProps {
  onAnalyze: (formData: FormData) => void;
  loading: boolean;
  validationErrors?: Record<string, string>;
}

// ============================================================================
// ANALYSIS PARAMETERS FORM
// ============================================================================
const AnalysisParametersForm = ({ onAnalyze, loading, validationErrors = {} }: AnalysisParametersFormProps) => {
  const { isDark } = useTheme();
  const styles = createStyles(isDark);
  
  // Form state
  const [formData, setFormData] = useState<FormData>({
    survey: "",
    year: "",
    gender: "",
    ageMin: "",
    ageMax: "",
    resultType: "summary",
    keyword: ""
  });

  const isValid = Boolean(formData.survey && formData.year);

  const handleInputChange = (field: keyof FormData, value: string) => {
    setFormData(prev => ({ ...prev, [field]: value }));
  };

  const handleSubmit = () => {
    if (isValid && !loading) {
      onAnalyze(formData);
    }
  };

  const surveyOptions = [
    { value: "", label: "Choose your dataset..." },
    { value: "PLFS", label: "üìä PLFS - Periodic Labour Force Survey" },
    { value: "HCES", label: "üè† HCES - Household Consumption Expenditure Survey" }
  ];

  const yearOptions = [
    { value: "", label: "Select analysis year..." },
    ...Array.from({ length: 10 }, (_, i) => ({
      value: (2024 - i).toString(),
      label: `üìà ${2024 - i}`
    }))
  ];

  const genderOptions = [
    { value: "", label: "üåê All Genders" },
    { value: "male", label: "üë® Male" },
    { value: "female", label: "üë© Female" },
    { value: "other", label: "‚öß Other" }
  ];

  const resultTypeOptions = [
    { value: "summary", label: "üìä Summary Analysis" },
    { value: "detailed", label: "üî¨ Detailed Analysis" }
  ];

  return (
    <GlassCard style={{ marginBottom: '3rem' }} glowColor={isDark ? '#8b5cf6' : '#3b82f6'}>
      <div className="mb-12 text-center">
        <div 
          className="inline-flex items-center gap-4 mb-4 p-4 rounded-3xl border backdrop-blur-xl"
          style={{
            background: isDark 
              ? 'linear-gradient(135deg, rgba(139, 92, 246, 0.2), rgba(167, 139, 250, 0.3))'
              : 'linear-gradient(135deg, rgba(59, 130, 246, 0.15), rgba(99, 102, 241, 0.25))',
            borderColor: isDark 
              ? 'rgba(139, 92, 246, 0.3)' 
              : 'rgba(59, 130, 246, 0.3)'
          }}
        >
          <span className="text-3xl">üîç</span>
          <h2 
            className="m-0 text-4xl font-extrabold tracking-tight"
            style={{ 
              fontFamily: "'Inter', -apple-system, sans-serif",
              ...styles.gradientText
            }}
          >
            Analysis Parameters
          </h2>
        </div>
        <p 
          className="text-lg m-0 font-medium leading-relaxed"
          style={{ 
            color: isDark ? '#cbd5e1' : '#64748b',
            fontFamily: "'Inter', -apple-system, sans-serif"
          }}
        >
          Configure your survey data exploration with precision controls
        </p>
      </div>

      {/* Main Form Fields */}
      <div className="grid grid-cols-1 md:grid-cols-2 gap-8 mb-10">
        <FormField label="üìã Survey Dataset *" error={validationErrors.survey}>
          <select 
            value={formData.survey} 
            onChange={(e) => handleInputChange('survey', e.target.value)} 
            style={styles.formInput}
            className="w-full"
          >
            {surveyOptions.map(option => (
              <option key={option.value} value={option.value}>
                {option.label}
              </option>
            ))}
          </select>
        </FormField>

        <FormField label="üìÖ Analysis Year *" error={validationErrors.year}>
          <select 
            value={formData.year} 
            onChange={(e) => handleInputChange('year', e.target.value)} 
            style={styles.formInput}
            className="w-full"
          >
            {yearOptions.map(option => (
              <option key={option.value} value={option.value}>
                {option.label}
              </option>
            ))}
          </select>
        </FormField>

        <FormField label="üë• Gender Filter">
          <select 
            value={formData.gender} 
            onChange={(e) => handleInputChange('gender', e.target.value)} 
            style={styles.formInput}
            className="w-full"
          >
            {genderOptions.map(option => (
              <option key={option.value} value={option.value}>
                {option.label}
              </option>
            ))}
          </select>
        </FormField>

        <FormField label="üéØ Analysis Type">
          <select 
            value={formData.resultType} 
            onChange={(e) => handleInputChange('resultType', e.target.value)} 
            style={styles.formInput}
            className="w-full"
          >
            {resultTypeOptions.map(option => (
              <option key={option.value} value={option.value}>
                {option.label}
              </option>
            ))}
          </select>
        </FormField>
      </div>

      {/* Optional Fields */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-8 mb-10">
        <FormField label="üéÇ Age Range (Optional)">
          <div 
            className="flex gap-4 items-center p-4 rounded-2xl border"
            style={{ 
              background: isDark ? 'rgba(15, 23, 42, 0.6)' : 'rgba(248, 250, 252, 0.8)',
              borderColor: isDark 
                ? 'rgba(139, 92, 246, 0.2)' 
                : 'rgba(59, 130, 246, 0.2)'
            }}
          >
            <input
              type="number"
              placeholder="Min age"
              value={formData.ageMin}
              onChange={(e) => handleInputChange('ageMin', e.target.value)}
              style={{ ...styles.formInput, maxWidth: '120px', margin: 0 }}
              className="max-w-[120px]"
            />
            <span 
              className="font-bold text-lg"
              style={{ 
                color: isDark ? '#cbd5e1' : '#64748b',
                fontFamily: "'Inter', -apple-system, sans-serif"
              }}
            >
              ‚ÜîÔ∏è to
            </span>
            <input
              type="number"
              placeholder="Max age"
              value={formData.ageMax}
              onChange={(e) => handleInputChange('ageMax', e.target.value)}
              style={{ ...styles.formInput, maxWidth: '120px', margin: 0 }}
              className="max-w-[120px]"
            />
          </div>
        </FormField>

        <FormField label="üîë Keywords (Optional)">
          <div className="relative">
            <input
              value={formData.keyword}
              onChange={(e) => handleInputChange('keyword', e.target.value)}
              placeholder="Enter search keywords to refine results..."
              style={styles.formInput}
              maxLength={100}
              className="w-full"
            />
            <div 
              className="absolute top-full right-0 text-sm mt-2 font-medium"
              style={{ 
                color: isDark ? '#94a3b8' : '#64748b',
                fontFamily: "'Inter', -apple-system, sans-serif"
              }}
            >
              {formData.keyword.length}/100 characters
            </div>
          </div>
        </FormField>
      </div>

      {/* Form Actions */}
      <div 
        className="flex justify-between items-center flex-wrap gap-6 p-8 rounded-3xl border"
        style={{ 
          background: isDark 
            ? 'linear-gradient(135deg, rgba(15, 23, 42, 0.6), rgba(30, 41, 59, 0.6))'
            : 'linear-gradient(135deg, rgba(248, 250, 252, 0.8), rgba(255, 255, 255, 0.8))',
          borderColor: isDark 
            ? 'rgba(148, 163, 184, 0.2)' 
            : 'rgba(203, 213, 225, 0.4)'
        }}
      >
        <StatusBadge 
          isValid={isValid} 
          validText="Ready to analyze" 
          invalidText="Missing required fields" 
        />
        
        <button
          onClick={handleSubmit}
          disabled={!isValid || loading}
          className="flex items-center gap-3 px-9 py-5 text-lg font-bold cursor-pointer transition-all duration-500 rounded-3xl border-2 border-white/20 backdrop-blur-xl"
          style={{
            background: (isValid && !loading) 
              ? (isDark 
                ? 'linear-gradient(135deg, #6366f1, #8b5cf6, #a855f7)' 
                : 'linear-gradient(135deg, #3b82f6, #6366f1, #8b5cf6)')
              : 'linear-gradient(135deg, rgba(156, 163, 175, 0.6), rgba(156, 163, 175, 0.8))',
            color: 'white',
            opacity: (isValid && !loading) ? 1 : 0.6,
            fontFamily: "'Inter', -apple-system, sans-serif",
            boxShadow: (isValid && !loading)
              ? (isDark 
                ? '0 12px 32px rgba(139, 92, 246, 0.4)' 
                : '0 12px 32px rgba(59, 130, 246, 0.4)')
              : 'none',
            cursor: (isValid && !loading) ? 'pointer' : 'not-allowed'
          }}
        >
          {loading ? (
            <>
              <LoadingSpinner />
              Analyzing Data...
            </>
          ) : (
            'üöÄ Start Deep Analysis'
          )}
        </button>
      </div>
    </GlassCard>
  );
};

export default AnalysisParametersForm;
