'use client';

// ============================================================================
// SHARED STYLES & UTILITIES
// ============================================================================
export const createStyles = (isDark: boolean) => ({
  glassmorphCard: (glowColor: string | null = null) => {
    const defaultGlow = isDark ? '#6d28d9' : '#3b82f6';
    const actualGlow = glowColor || defaultGlow;
    
    return {
      background: isDark 
        ? 'rgba(30, 41, 59, 0.55)' 
        : 'rgba(255, 255, 255, 0.55)',
      border: isDark 
        ? '1px solid rgba(148, 163, 184, 0.3)' 
        : '1px solid rgba(255, 255, 255, 0.7)',
      borderRadius: '20px',
      boxShadow: isDark 
        ? `0 8px 32px rgba(0, 0, 0, 0.3), 0 0 0 1px rgba(148, 163, 184, 0.1), 0 0 20px ${actualGlow}20` 
        : `0 8px 32px rgba(59, 130, 246, 0.15), 0 0 0 1px rgba(255, 255, 255, 0.3), 0 0 20px ${actualGlow}15`,
      backdropFilter: 'blur(20px)',
      WebkitBackdropFilter: 'blur(20px)',
      padding: '1.5rem',
      transition: 'all 0.4s cubic-bezier(0.4, 0, 0.2, 1)',
      position: 'relative' as const,
      overflow: 'hidden' as const
    };
  },
  
  formInput: {
    width: '100%',
    padding: '16px 20px',
    borderRadius: '16px',
    border: isDark 
      ? '2px solid rgba(139, 92, 246, 0.2)' 
      : '2px solid rgba(59, 130, 246, 0.2)',
    background: isDark 
      ? 'rgba(15, 23, 42, 0.8)' 
      : 'rgba(255, 255, 255, 0.9)',
    color: isDark ? '#f8fafc' : '#1e293b',
    fontSize: '1rem',
    backdropFilter: 'blur(16px)',
    WebkitBackdropFilter: 'blur(16px)',
    transition: 'all 0.3s cubic-bezier(0.4, 0, 0.2, 1)',
    outline: 'none',
    fontFamily: "'Inter', -apple-system, sans-serif",
    fontWeight: '500',
    boxShadow: isDark 
      ? 'inset 0 2px 4px rgba(0, 0, 0, 0.4), 0 1px 2px rgba(139, 92, 246, 0.1)'
      : 'inset 0 2px 4px rgba(0, 0, 0, 0.06), 0 1px 2px rgba(59, 130, 246, 0.1)'
  },
  
  label: {
    display: 'block',
    marginBottom: '12px',
    fontWeight: '700',
    color: isDark ? '#f8fafc' : '#1e293b',
    fontSize: '1rem',
    fontFamily: "'Inter', -apple-system, sans-serif",
    textShadow: isDark ? '0 2px 4px rgba(0, 0, 0, 0.5)' : 'none',
    letterSpacing: '0.01em'
  },
  
  gradientText: {
    backgroundImage: isDark 
      ? 'linear-gradient(135deg, #ffffff, #e2e8f0)'
      : 'linear-gradient(135deg, #1e293b, #475569)',
    WebkitBackgroundClip: 'text',
    WebkitTextFillColor: 'transparent',
    backgroundClip: 'text'
  }
});